


.form_title {
	color: #646cff;
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 22px;
	line-height: 23px;
}


.cmp_dtls{
	padding: 15px 3px;
}


.cmp_desc{
	margin-top: 25px;
	padding: 15px 3px;
	border-top: 1px solid #dadce0;
	text-align: left;
}

.cmp_desc .description{
	margin-left: 12px;
}


.desc_lbl{
	margin-right: 8px;
}

.hdr_bar{
	padding: 10px 7px;
	border-radius: 8px;
}






.boxAddAcc{
	padding: 16px;
    border: 1px solid var(--color_text_dad);
    border-radius: 8px;
}



.icon svg{
	cursor: pointer;
}














.acc_dtls{
	padding: 0 11px 11px;
	margin-left: 30px;
}

.acc_dtls .code{
	margin-right: 12px;
	font-weight: 600;
	font-size: 18px;
	line-height: 18px;
}
.acc_dtls .name{
	font-weight: 500;
	line-height: 18px;
}



.comprobante_id{
	color: #646cff;
	font-weight: 600;
	cursor: pointer;
}



.tr_bar_s6{
	flex-basis: 16.66666%;
	width: 16.66666%;
}

.tr_bar_20{
	flex-basis: 20%;
	width: 20%;
}

.tr_bar_60{
	flex-basis: 60%;
	width: 60%;
}


.subMHeight{
	height:  calc(70vh - 100px)
}




.left_alg{
	text-align: left;
	padding-left: 14; 
}


.subMheader{
	font-family: "Google Sans","GoogleSans-Local";
}



.lbl_prod_header{
    color: #80868b;
    font: 500 22px/34px Google Sans,GoogleSans-Local,Roboto,sans-serif;
    letter-spacing: .2px;
    text-align: center;
}

.lbl_prod_amount{
	color: #30363b;
    font: 500 50px/56px Google Sans,GoogleSans-Local,Roboto,sans-serif;
    letter-spacing: .2px;
	text-align: center;
	margin: 0 10px;
	width: 90px;
	justify-content: space-around;
}

.code_prod_header{
	color: #40464b;
    font: 500 17px/34px Google Sans,GoogleSans-Local,Roboto,sans-serif;
    letter-spacing: .2px;
	text-align: center;
}

.lbl_prod_price{
    color: #60666b;
    font: 700 28px/38px Google Sans,GoogleSans-Local,Roboto,sans-serif;
    letter-spacing: .2px;
    text-align: center;
}


.btns_control_2cart{
	padding: 15px 4px;
    margin: 0 15px;
    width: 50%;
}


.iconFlx{
	display: flex;
	align-content: center;
	align-items: center;
}

.iconFlx svg{
	cursor: pointer;
	transition: transform 0.25s;
	border-radius: 50%;
	padding: 8px;
}


.iconFlx svg:hover{
	box-shadow: 0 1px 7px rgb(0 0 0 / 19%);
    transform: scale(1.15);
	z-index: 9;
	
}



